/* Set z-index of everything to minus one so canvas shows up for Konami code ðŸ¤« */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  z-index: -1;
}
@font-face {
  font-family: dosFont;
  src: url(font.ttf);
}
canvas {
  height: 100vh;
  width: 100vw;
  z-index: 1;
}
body {
  font-family: dosFont;
  color: white;

  background-color: rgb(35, 35, 35);
}
.text {
  font-size: 5.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: chartreuse;
}
h1 {
  font-size: 1.4rem;
  padding-bottom: 1rem;
  color: wheat;
}
a {
  font-size: 2rem;
  position: absolute;
  color: wheat;
}
.linkedin {
  margin-left: 1.5em;
}

.twitter {
  margin-left: 3em;
}

.blinking{
  animation:blinkingText 1.2s infinite;
}
@keyframes blinkingText{
  0%{     color: red;    }
  49%{    color: red; }
  60%{    color: transparent; }
  99%{    color:transparent;  }
  100%{   color: red;    }
}